body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  background-color: #EDF7F9;
}

.item {
  border: 5px solid lightskyblue;
  -webkit-box-shadow: 15px 18px 24px -13px rgba(0,0,0,0.75);
  box-shadow: 15px 18px 24px -13px rgba(0,0,0,0.75);
  padding: 10px;
  margin:20px 10px;
  border-radius: 5px;
}

.input {
  margin-left: 43%;
  margin-top: 10px;
  padding: 5px;
  border-radius: 10px;
  border: 1px solid lightskyblue;
  outline: none; 
}

.svg {
  width: 500px;
  height: 500px;
  display:inline-block;
  text-align: center;
  position: absolute;
  top: 100px;
  left: 30%;
}


